.donation-container {
	display: flex;
}

.donation-currency {
	padding: 1.5rem 1.3rem;
	position: absolute;
	font-size: 2rem;
	letter-spacing: -0.015em;
	color: #6d6d6d;
}

.donate-text-box {
	display: inline-block !important;
	width: 180px !important;
	height: 58px !important;
	padding: 1.5rem 1.8rem 1.5rem 2.7rem !important;
	font-size: 1.6rem !important;
	letter-spacing: -0.015em;
	margin: 0;
	max-width: 100%;
	padding: 1.5rem 1.8rem;
	background: #fff;
	border-radius: 0;
	border-style: solid;
	border-width: 0.1rem;
	box-shadow: none;
}

.donate-button {
	display: inline-block !important;
	padding: 1.5rem 1.8rem !important;
	width: 100%;
	background: #cd2653;
	border: none;
	border-radius: 0;
	color: #fff;
	cursor: pointer;
	font-size: 1.5rem;
	font-weight: 600;
	letter-spacing: 0.0333em;
	line-height: 1.25;
	margin: 0;
	opacity: 1;
	padding: 1.1em 1.44em;
	text-align: center;
	text-decoration: none;
	text-transform: uppercase;
	transition: opacity 0.15s linear;
}
